//  router
import router from '../../router';
//  html 
import template from './index.html';
//  css,  <style>  <head> 
import './style.css';

// 
export default class {
  mount(container) {
    document.title = 'foo';
    container.innerHTML = template;
    container.querySelector('.foo__gobar').addEventListener('click', () => {
      //  router.go  /bar 
      router.go('/bar');
    });
  }
}
