//  router
import router from '../../router';
//  html 
import template from './index.html';
//  css,  <style>  <head> 
import './style.css';

// 
export default class {
  mount(container) {
    document.title = 'bar';
    container.innerHTML = template;
    container.querySelector('.bar__gofoo').addEventListener('click', () => {
      //  router.go  /foo 
      router.go('/foo');
    });
  }
}
